<template>
	<el-row class="container">
		<el-col :span="24" class="header">
			<el-col :span="10" class="logo" :class="'logo-width'">
				<div class="tools" @click.prevent="collapse">
					<i class="fa fa-bars"></i> <span> {{sysName}}</span>
				</div>
				 
			</el-col>
			
			<el-col :span="14" class="userinfo">
				
				<!--<el-select  placeholder="PricewaterhouseCoopers Zhong Tian CPAs Limited Company">
				<el-option label="PricewaterhouseCoopers Zhong Tian CPAs Limited Company" value="1"></el-option>
				<el-option label="PricewaterhouseCoppers Beijing Office" value="2"></el-option>
				<el-option label="Organization3" value="3"></el-option>
				</el-select>-->

				<el-dropdown >
					<span class="el-dropdown-link userinfo-inner"><img :src="this.sysUserAvatar" />{{sysUserName}} </span>
					<!--<el-dropdown-menu slot="dropdown">
						<el-dropdown-item>我的消息</el-dropdown-item>
						<el-dropdown-item>设置</el-dropdown-item>
						<el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
					</el-dropdown-menu>-->
				</el-dropdown>
			</el-col>
		</el-col>
		<el-col :span="24" class="main">
			<aside :class="collapsed?'menu-collapsed':'menu-expanded'">
				<!--导航菜单-->
				<el-menu :default-active="$route.path" class="el-menu-vertical-demo" @open="handleopen" @close="handleclose" @select="handleselect"
					 unique-opened router v-show="!collapsed">
					 
					<template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
						<el-submenu :index="index+''" v-if="!item.leaf">
							<template slot="title"><i :class="item.iconCls"></i>{{item.name}}</template>
							<el-menu-item v-for="child in item.children" :index="child.path" :key="child.path" v-if="!child.hidden">{{child.name}}</el-menu-item>
						</el-submenu>
						<el-menu-item v-if="item.leaf&&item.children.length>0" :index="item.children[0].path"><i :class="item.iconCls"></i>{{item.children[0].name}}</el-menu-item>
					</template>
				</el-menu>
				<!--导航菜单-折叠后-->
				<ul class="el-menu el-menu-vertical-demo collapsed" v-show="collapsed" ref="menuCollapsed">
					<li v-for="(item,index) in $router.options.routes" v-if="!item.hidden" class="el-submenu item" >
						<template v-if="!item.leaf">
							<div class="el-submenu__title" style="padding-left: 20px;" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)"><i :class="item.iconCls"></i></div>
							<ul class="el-menu submenu" :class="'submenu-hook-'+index" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)"> 
								<li v-for="child in item.children" v-if="!child.hidden" :key="child.path" class="el-menu-item" style="padding-left: 40px;" :class="$route.path==child.path?'is-active':''" @click="$router.push(child.path)">{{child.name}}</li>
							</ul>
						</template>
						<template v-else>
							<li class="el-submenu">
								<div class="el-submenu__title el-menu-item" style="padding-left: 20px;height: 56px;line-height: 56px;padding: 0 20px;" :class="$route.path==item.children[0].path?'is-active':''" @click="$router.push(item.children[0].path)"><i :class="item.iconCls"></i></div>
							</li>
						</template>
					</li>
				</ul>
			</aside>
			<section class="content-container">
				<div class="grid-content bg-purple-light">
					<el-col :span="24" class="breadcrumb-container">
						<strong class="title">{{$route.name}}</strong>
						<!-- <el-breadcrumb separator="/" class="breadcrumb-inner">
							<el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
								{{ item.name  }}
							</el-breadcrumb-item>
						</el-breadcrumb> -->
					</el-col>
					<el-col :span="24" class="content-wrapper">
						<transition name="fade" mode="out-in">
							<router-view></router-view>
						</transition>
					</el-col>
				</div>
			</section>
		</el-col>
	</el-row>
</template>

<script>
	export default {
		data() {
			return {
				sysName:'AdminSite',//'eFinance',
				collapsed:false,
				sysUserName: 'Ge ',
				sysUserAvatar: 'http://10.150.89.107/ImageWebApi/api/Image/CN414436?type=1',
				logourl: 'http://cnhktaxuat/TEAM/Static/Images/pwclogo.png',
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				}
			}
		},
		methods: {
			onSubmit() {
				console.log('submit!');
			},
			handleopen() {
				console.log('handleopen');
			},
			handleclose() {
				console.log('handleclose');
			},
			handleselect: function (a, b) {
			},
			//退出登录
			logout: function () {
				var _this = this;
				this.$confirm('确认退出吗?', '提示', {
					//type: 'warning'
				}).then(() => {
					sessionStorage.removeItem('user');
					_this.$router.push('/login');
				}).catch(() => {

				});


			},
			//折叠导航栏
			collapse:function(){
				this.collapsed=!this.collapsed;
			},
			showMenu(i,status){
				this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-'+i)[0].style.display=status?'block':'none';
			}
		},
		mounted() {
			var user = sessionStorage.getItem('user');
			if (user) {
				user = JSON.parse(user);
				this.sysUserName = user.name || '';
				this.sysUserAvatar = user.avatar || '';
			}

		}
	}

</script>

<style scoped lang="scss">
	@import '~scss_vars';

	.text-orange{
		color:#ff851b !important
	}
	.text-red{
		color:#dd4b39 !important
	}

	
	.container {
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		.header {
			height: 40px;
			line-height: 40px;
			background: $color-primary;
			color:#fff;
			.userinfo {
				text-align: right;
				padding-right: 25px;				
				float: right;
				.userinfo-inner {
					cursor: pointer;
					color:#fff;
					img {
						width: 25px;
						height: 25px;
						border-radius: 20px;
						margin: 10px 0px 10px 10px;
						float: right;
					}
					i{
						
						height: 35px;
						border-radius: 20px;
						margin: 18px 0px 10px 4px;
						float: right;
					}					
				}
				.userOrgazation{
					padding-right:20px;
				}
				.input-with-select .el-input-group__prepend {
					background-color: orange;
				}
			}
			.logo {
				//width:230px;
				height:40px;
				font-size: 22px;
				padding-left:12px;
				padding-right:20px;
				margin: 0px 0px 0px 10px;
				font-size: 17px;
				//border-color: rgba(238,241,146,0.3);
				//border-right-width: 1px;
				//border-right-style: solid;
				img {
					width: 35px;
					float: left;
					margin: 10px 10px 10px 18px;
				}
				.txt {
					color:#fff;
				}
			}
			.logo-width{
				width:230px;
			}
			.logo-collapse-width{
				width:50px
			}
			
			.tools{
				//padding: 0px 23px;
				//width:14px;
				height: 40px;
				line-height: 40px;
				
				i{
						margin: 12px 12px 10px 0px;
						cursor: pointer;
				}	
				
			}
		}
		.main {
			display: flex;
			// background: #324057;
			position: absolute;
			top: 40px;
			bottom: 0px;
			overflow: hidden;
			aside {
				flex:0 0 230px;
				width: 230px;
				// position: absolute;
				// top: 0px;
				// bottom: 0px;
				.el-menu{
					height: 100%;
				}
				.el-menu-item{
					min-width:0px;
				}
				.collapsed{
					width:55px;
					.item{
						position: relative;
					}
					.submenu{
						position:absolute;
						top:0px;
						left:55px;
						z-index:99999;
						height:auto;
						display:none;
						
					}

				}
			}
			.menu-collapsed{
				flex:0 0 55px;
				width: 55px;
			}
			.menu-expanded{
				flex:0 0 230px;
				width: 230px;
				.el-menu {
				width: 100% !important;
				}
				.el-submenu .el-menu-item {
				min-width: 0px !important;
				}
			}
			.content-container {
				// background: #f1f2f7;
				flex:1;
				// position: absolute;
				// right: 0px;
				// top: 0px;
				// bottom: 0px;
				// left: 230px;
				overflow-y: scroll;
				padding: 20px;
				.breadcrumb-container {
					//margin-bottom: 15px;
					.title {
						width: 200px;
						float: left;
						color: #ff851b;
						font-size:16px;
						margin: 0px 0px 10px 0px;						
					}
					.breadcrumb-inner {
						float: right;
					}
				}
				.content-wrapper {
					background-color: #fff;
					box-sizing: border-box;
				}
			}
		}
	}
</style>


